.info__image {
    height: 300px;
    padding: 10px;
    display: flex;
    align-items: center;
    margin-top: -100px;
    background-color: #ffffff;
    border-radius: 5px;
}

.info__image img {
    width: 300px;
    height: 250px;
    object-fit: cover;
    border-radius: 5px;
}

.info__text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin-left: 15px;
}

.info__text h3 {
    margin: 0 0 12px 0;
}

.info__text a {
    text-decoration: none;
    color: #515355;
}

.icon {
    display: inline-block;
    height: 35px;
    width: 35px;
    position: relative;
    left: -50px;
    bottom: -90px;
}

.wrapper__information {
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 0 10px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.about__personal_information {
    max-width: 280px;
    width: 100%;
    background-color: #ffffff;
    padding: 20px;
    border-radius: 5px;
}

.about__details_information {
    width: 80%;
    margin-left: 10px;
    background-color: #ffffff;
}

.about__header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 12px;
}

.information__list {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.information__list li {
    margin-bottom: 10px;
    font-size: 15px;
    color: #515355;
}

.information__list a {
    font-size: 15px;
    color: #515355;
    text-decoration: none;
}

.information__list li span {
    font-weight: 600;
}

.title {
    font-size: 18px;
    font-weight: 600;
}

.about__details_information {
    padding: 10px;
}

.about__details_information h3 {
    margin-top: 0;
    font-size: 18px;
    font-weight: 600;
    text-align: left;
}

.about__details_information p {
    text-align: left;
    color: #515355;
}